import request from '@/common/js/request.js'
export default {
	// 
	getUserBalance() {
		return request({
			url: `/pay/user/balance`,
			method: 'GET'
		})
	},
	// -IOS
	orderPay(data) {
		return request({
			url: '/pay/order/pay',
			method: 'POST',
			data
		})
	},
	// -
	getOrderInfoWxpay(data) {
		return request({
			url: '/pay/orderInfo/wxpay',
			method: 'POST',
			data
		})
	},
	// -
	getOrderInfoAlipay(data) {
		return request({
			url: '/pay/orderInfo/alipay',
			method: 'POST',
			data
		})
	},
	// 
	getUserOrderList() {
		return request({
			url: '/pay/order/user/list',
			method: 'GET'
		})
	},
	// 
	deleteOrder(orderId) {
		return request({
			url: `/pay/order/${orderId}`,
			method: 'DELETE'
		})
	},
	// 
	cancelOrder(orderId) {
		return request({
			url: `/pay/order/cancel/${orderId}`,
			method: 'PUT'
		})
	},
}