/**
 * toast 
 */
export const msg = (title = '', param = {}) => {
	if (!title) return;
	uni.showToast({
		title,
		duration: param.duration || 1500,
		mask: param.mask || false,
		icon: param.icon || 'none'
	});
}

/**
 * 
 * @return {Boolean}
 */
export const isLogin = (options = {}) => {
	const token = uni.getStorageSync('mxgEducationToken');
	if (token) { // !
		return true;
	}
	// options.nav false
	if (options.nav !== false) {
		// console.log('options.nav', options.nav)
		uni.navigateTo({
			url: '/pages/auth/login'
		})
	}
	return false;
}

// 
let _throttleRunning = false
/**
 * 
 * @param {Function} 
 * @param {Number} delay ms  
 */
export const throttle = (fn, delay = 500) => {
	// 
	if (_throttleRunning) {
		return;
	}
	//
	_throttleRunning = true;

	// delay
	setTimeout(() => {
		fn();
		_throttleRunning = false;
	}, delay);
}

/**
 * +
 */
export const routePath = () => {
	// 
	const pages = getCurrentPages();
	// 
	const page = pages[pages.length - 1];

	//  {id: 2, name: 'xx'}
	// app page.options ; H5 page.$route.query
	const query = page.options || page.$route.query;
	//  key1=value1&key2=value2
	let queryStr = '';
	query && Object.keys(query).forEach((key) => {
		queryStr += `${key}=${query[key]}`
	});
	// + pages/course/course-details.vue?key1=value1&key2=value2
	return queryStr ? `${page.route}?${queryStr}` : page.route;
}

/**
 * 
 * @param 
 * @param 
 */
export const checkStr = (str, type) => {
	switch (type) {
		case 'mobile': //
			return /^1[3|4|5|6|7|8|9][0-9]{9}$/.test(str);
		case 'tel': //
			return /^(0\d{2,3}-\d{7,8})(-\d{1,4})?$/.test(str);
		case 'card': //
			return /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(str);
		case 'mobileCode': //6
			return /^[0-9]{6}$/.test(str)
		case 'pwd': //6~18
			return /^([a-zA-Z0-9_]){6,18}$/.test(str)
		case 'payPwd': // 6
			return /^[0-9]{6}$/.test(str)
		case 'postal': //
			return /[1-9]\d{5}(?!\d)/.test(str);
		case 'QQ': //QQ
			return /^[1-9][0-9]{4,9}$/.test(str);
		case 'weixin': //
			return /^[a-zA-Z][a-zA-Z\d_-]{5,19}$/.test(str);
		case 'email': //
			return /^[\w-]+(\.[\w-]+)*@[\w-]+(\.[\w-]+)+$/.test(str);
		case 'money': //(2)
			return /^\d*(?:\.\d{0,2})?$/.test(str);
		case 'URL': //
			return /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/.test(str)
		case 'IP': //IP
			return /((?:(?:25[0-5]|2[0-4]\\d|[01]?\\d?\\d)\\.){3}(?:25[0-5]|2[0-4]\\d|[01]?\\d?\\d))/.test(str);
		case 'date': //
			return /^(\d{4})\-(\d{2})\-(\d{2}) (\d{2})(?:\:\d{2}|:(\d{2}):(\d{2}))$/.test(str) ||
				/^(\d{4})\-(\d{2})\-(\d{2})$/
				.test(str)
		case 'number': //
			return /^[0-9]$/.test(str);
		case 'english': //
			return /^[a-zA-Z]+$/.test(str);
		case 'chinese': //
			return /^[\\u4E00-\\u9FA5]+$/.test(str);
		case 'lower': //
			return /^[a-z]+$/.test(str);
		case 'upper': //
			return /^[A-Z]+$/.test(str);
		case 'HTML': //HTML
			return /<("[^"]*"|'[^']*'|[^'">])*>/.test(str);
		default:
			return true;
	}
}

/**
 * @desc 
 * @param { Nubmer} - Date , 
 * @returns { String } 
     // 20120110 12:46
     //
    //16
    //10:10
    //10:10
    //0210 10:10:11
    //20121010 10:10:11
 */
export const dateFormat = (date) => {
	if (!date) return ''
	//new Date  ios safariios2027-2-22 16:232027/2/22 16:23 
	// ?  ios safari : 
	let $this = new Date(date) == 'Invalid Date' ? new Date(date.replace(/-/g, "/")) : new Date(date)
	var timestamp = parseInt(Date.parse($this)) / 1000 //- 8 * 60 * 60; //8;

	function zeroize(num) {
		return (String(num).length == 1 ? '0' : '') + num;
	}
	var curTimestamp = parseInt(new Date().getTime() / 1000); //
	var timestampDiff = curTimestamp - timestamp; // 
	var curDate = new Date(curTimestamp * 1000); // 
	var tmDate = new Date(timestamp * 1000); // 

	var Y = tmDate.getFullYear(),
		m = tmDate.getMonth() + 1,
		d = tmDate.getDate();
	var H = tmDate.getHours(),
		i = tmDate.getMinutes(),
		s = tmDate.getSeconds();


	if (timestampDiff < 60) { // 
		return "";
	} else if (timestampDiff < 3600) { // 
		return Math.floor(timestampDiff / 60) + "";
	} else if (curDate.getFullYear() == Y && curDate.getMonth() + 1 == m && curDate.getDate() == d) {
		return ' ' + zeroize(H) + ':' + zeroize(i) + ':' + zeroize(s);
	} else {
		var newDate = new Date((curTimestamp - 86400) * 1000); // 
		if (newDate.getFullYear() == Y && newDate.getMonth() + 1 == m && newDate.getDate() == d) {
			return ' ' + zeroize(H) + ':' + zeroize(i) + ':' + zeroize(s);
		} else if (curDate.getFullYear() == Y) {
			// return  zeroize(m) + '' + zeroize(d) + ' ' + zeroize(H) + ':' + zeroize(i) + ':' + zeroize(s);
			return zeroize(m) + '' + zeroize(d) + '';
		} else {
			// return  Y + '' + zeroize(m) + '' + zeroize(d) + ' ' + zeroize(H) + ':' + zeroize(i) + ':' + zeroize(s);
			return Y + '' + zeroize(m) + '' + zeroize(d) + '';
		}
	}
}
/**
 * @param {format} yyyy-MM-dd hh:mm:ss
 */
export function format(date, format = "yyyy-MM-dd hh:mm:ss") {
	if (!date) return ''
	//new Date  ios safari
	// ? safari : 
	let $this = new Date(date) == 'Invalid Date' ? new Date(date.substr(0, 19)) : new Date(date)

	let o = {
		'M+': $this.getMonth() + 1,
		'd+': $this.getDate(),
		'h+': $this.getHours(),
		'm+': $this.getMinutes(),
		's+': $this.getSeconds(),
		'q+': Math.floor(($this.getMonth() + 3) / 3),
		'S': $this.getMilliseconds()
	}
	if (/(y+)/.test(format)) {
		format = format.replace(RegExp.$1, ($this.getFullYear() + '').substr(4 - RegExp.$1.length))
	}
	for (var k in o) {
		if (new RegExp('(' + k + ')').test(format)) {
			format = format.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
		}
	}
	return format
}