//  store 
import store from '@/store'
const rightIcon = 'iconfont icon-right' // 
export default () => {
	// 
	const userInfo = store.state.userInfo
	return [ // 
		[{
				title: '',
				event: 'chooseImg', // 
				src: userInfo.imageUrl || '/static/logo.png', // 
				rightIcon, // 
			},
			{
				title: '',
				event: 'editUsername',
				text: userInfo.username,
				rightIcon,
			},
			{
				title: '',
				event: 'editMobile',
				text: userInfo.mobile,
				rightIcon,
			}
		],
		[{
				title: '',
				event: 'editName',
				text: userInfo.nickName,
				rightIcon,
			},
			{
				title: '',
				event: 'chooseSex',
				text: userInfo.sex ? '' : '',
				rightIcon,
			}
		],
	]
}