/**
 * mescroll-more-itemmixins,  mescroll-body  ( mescroll-more )
 */
const MescrollMoreItemMixin = {
	// propsmixin,

	props:{
		i: Number, // tab
		index: { // tab
			type: Number,
			default(){
				return 0
			}
		}
	},

	data() {
		return {
			downOption:{
				auto:false // 
			},
			upOption:{
				auto:false // 
			},
			isInit: false // tab
		}
	},
	watch:{
		// 
		index(val){
			if (this.i === val && !this.isInit) this.mescrollTrigger()
		}
	},
	methods: {
		// mescroll,mescroll (mescroll-mixins.jsmescrollInit, isInit)
		mescrollInit(mescroll) {
			this.mescroll = mescroll;
			// tab
			if(this.i === this.index){
				this.mescrollTrigger()
			}
		},
		// 
		mescrollTrigger(){
			this.isInit = true; // true
			if (this.mescroll) {
				if (this.mescroll.optDown.use) {
					this.mescroll.triggerDownScroll();
				} else{
					this.mescroll.triggerUpScroll();
				}
			}
		}
	}
}

export default MescrollMoreItemMixin;
