/*  */
MeScroll.prototype.getScrollBottom = function() {
	return this.getScrollHeight() - this.getClientHeight() - this.getScrollTop()
}

/* 
 star: 
 end: 
 callback(step,timer): step,timer,window.clearInterval(timer);
 t: ,0end;300ms
 rate: ;30ms
 * */
MeScroll.prototype.getStep = function(star, end, callback, t, rate) {
	let diff = end - star; // 
	if (t === 0 || diff === 0) {
		callback && callback(end);
		return;
	}
	t = t || 300; //  300ms
	rate = rate || 30; //  30ms
	let count = t / rate; // 
	let step = diff / count; // 
	let i = 0; // 
	let timer = setInterval(function() {
		if (i < count - 1) {
			star += step;
			callback && callback(star, timer);
			i++;
		} else {
			callback && callback(end, timer); // end,
			clearInterval(timer);
		}
	}, rate);
}

/*  */
MeScroll.prototype.getClientHeight = function(isReal) {
	let h = this.clientHeight || 0
	if (h === 0 && isReal !== true) { // ,body ()
		h = this.getBodyHeight()
	}
	return h
}
MeScroll.prototype.setClientHeight = function(h) {
	this.clientHeight = h;
}

/*  */
MeScroll.prototype.getScrollHeight = function() {
	return this.scrollHeight || 0;
}
MeScroll.prototype.setScrollHeight = function(h) {
	this.scrollHeight = h;
}

/* body */
MeScroll.prototype.getBodyHeight = function() {
	return this.bodyHeight || 0;
}
MeScroll.prototype.setBodyHeight = function(h) {
	this.bodyHeight = h;
}

/*  */
MeScroll.prototype.preventDefault = function(e) {
	// e.preventDefault, wxs
	// appbouncepages.jsonstyle.app-plus.bounce"none", renderjs
	// cancelable:; defaultPrevented:
	if (e && e.cancelable && !e.defaultPrevented) e.preventDefault()
}