import request from '@/common/js/request.js'
export default {
	// 
	addFeedback(data) {
		return request({
			url: `/system/api/feedback`,
			method: 'POST',
			data
		})
	},
	// 
	sendSmsCode(data) {
		return request({
			url: '/system/sms/code',
			method: 'POST',
			data
		})
	},
	// 
	login(data) {
		return request({
			url: '/auth/login',
			method: 'POST',
			data
		})
	},
	// 
	loginByProvider(data) {
		return request({
			url: '/auth/login/provider',
			method: 'POST',
			data
		})
	},
	// 
	updateUserMobile(data) {
		return request({
			url: '/auth/user/mobile',
			method: 'PUT',
			data
		})
	},
}