// wxs (renderjs) , 
const WxsMixin = {
	data() {
		return {
			// wxs ()
			wxsProp: {
				optDown:{}, // 
				scrollTop:0, // 
				bodyHeight:0, // body
				isDownScrolling:false, // 
				isUpScrolling:false, // 
				isScrollBody:true, // mescroll-body
				isUpBoth:true, // ,
				t: 0 //  (,wxsObserver)
			},
			
			// wxs
			callProp: {
				callType: '', // 
				t: 0 //  (,wxsObserver)
			},
			
			// wxswxsBiz,wxs (APPwxsBiz./wxs/wxs.wxs)


















			
			// renderjsrenderBiz,renderjs (app  h5 renderBiz./wxs/renderjs.js)

			renderBiz: {
				propObserver(){} // renderjs
			}

		}
	},
	methods: {
		// wxs
		wxsCall(msg){
			if(msg.type === 'setWxsProp'){
				// wxsProp ()
				this.wxsProp = {
					optDown: this.mescroll.optDown,
					scrollTop: this.mescroll.getScrollTop(),
					bodyHeight: this.mescroll.getBodyHeight(),
					isDownScrolling: this.mescroll.isDownScrolling,
					isUpScrolling: this.mescroll.isUpScrolling,
					isUpBoth: this.mescroll.optUp.isBoth,
					isScrollBody:this.mescroll.isScrollBody,
					t: Date.now()
				}
			}else if(msg.type === 'setLoadType'){
				// inOffset,outOffset
				this.downLoadType = msg.downLoadType
				// mescroll, , <me-video>
				this.$set(this.mescroll, 'downLoadType', this.downLoadType)
				// 
				this.$set(this.mescroll, 'isDownEndSuccess', null)
			}else if(msg.type === 'triggerDownScroll'){
				// 
				this.mescroll.triggerDownScroll();
			}else if(msg.type === 'endDownScroll'){
				// 
				this.mescroll.endDownScroll();
			}else if(msg.type === 'triggerUpScroll'){
				// 
				this.mescroll.triggerUpScroll(true);
			}
		}
	},
	mounted() {

		// wxs
		this.mescroll.optDown.afterLoading = ()=>{
			this.callProp = {callType: "showLoading", t: Date.now()} // wxs ()
		}
		// wxs
		this.mescroll.optDown.afterEndDownScroll = ()=>{
			this.callProp = {callType: "endDownScroll", t: Date.now()} // wxs ()
			let delay = 300 + (this.mescroll.optDown.beforeEndDelay || 0)
			setTimeout(()=>{
				if(this.downLoadType === 4 || this.downLoadType === 0){
					this.callProp = {callType: "clearTransform", t: Date.now()} // wxs ()
				}
				// mescroll, , <me-video>
				this.$set(this.mescroll, 'downLoadType', this.downLoadType)
			}, delay)
		}
		// wxs
		this.wxsCall({type: 'setWxsProp'})

	}
}

export default WxsMixin;
