import {
	request
} from '@/common/js/request'

export default {
	data() {
		return {
			page: 2,
		}
	},
	methods: {
		/**
		 * 
		 * @param {String} url 
		 * @param {Object} options 
		 * @param {Boolean} options.login   
		 */
		navTo(url, options = {}) {
			if (!url) {
				return;
			}
			if (options.login && !this.$store.getters.hasLogin) {
				url = '/pages/auth/login';
			}
			uni.navigateTo({
				url
			})
		},

		// , 
		navBack(delta = 1) {
			// console.log('navBack', delta)
			uni.navigateBack({
				delta
			});
		},
	}
}